@import "@/assets/global.scss";






































































































.main-content {
  box-sizing: border-box;
  margin-top: 24px;
  padding: 52px 16px 16px 16px;
}

.test-table {
  width: 100%;
  max-width: 328px;
  border-collapse: collapse;
  margin: 0 auto 20px auto;

  > .table-row:first-child {
    display: flex;
    border-top: 1px solid #CBCBCB;
    border-bottom: 1px solid #CBCBCB;
  }

  > .table-row {
    display: flex;
    border-bottom: 1px solid #CBCBCB;
  }
}

.table-col:first-child {
  @extend .font-14-normal;
  padding: 12px;
  text-align: left;
  border-left: 1px solid #CBCBCB;
  border-right: 1px solid #CBCBCB;
}

.table-col {
  @extend .font-14-normal;
  padding: 12px;
  text-align: left;
  border-right: 1px solid #CBCBCB;
}

.table-col-btn:first-child {
  @extend .font-14-normal;
  padding: 12px;
  text-align: center;
  color: #CBCBCB;
  border-left: 1px solid #CBCBCB;
}

.table-col-btn:last-child {
  @extend .font-14-normal;
  padding: 12px;
  text-align: center;
  color: #CBCBCB;
  border-right: 1px solid #CBCBCB;
}

.available {
  cursor: pointer;
  color: #C8553D!important;
}
